import React, { useRef, useState, useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { useRouter, useRoute } from 'react-router5';
import { useKeyboard } from '../../components/useKeyboard/useKeyboard';

import styled from '@emotion/styled';
import { mdiAccountCircleOutline, mdiMenu, mdiArrowLeft } from '@mdi/js';
import { Fade } from '../../components/Fade/Fade';
import { Icon, EmpIcon } from '../../components/Icon/Icon';
import { Input as UInput } from '../../components/Input/Input';
import { HeaderBar } from './Bar/HeaderBar';
import { HeaderAccountMenu } from './AccountMenu/HeaderAccountMenu';

import { toggleSideBar, setSearch } from '../../store/actions/ui';
import { ReduxState } from '../../store/store';

const MenuIcon = styled(EmpIcon)`
  padding: 0.5em;
  width: 2.5em;
  height: 2.5em;
  margin-left: 0.5em;
`;
const AccountIcon = styled(Icon)`
  padding: 0.5em;
  width: 2.5em;
  height: 2.5em;
  margin-right: 0.5em;
`;
const Input = styled(UInput)`
  font-size: 1rem;
`;
export const Header: React.FC = () => {
  const inputRef = useRef<HTMLInputElement>(null);
  const [menuOpened, setMenuOpened] = useState(false);
  const [searching, setSearching] = useState(false);
  const [prevRoute, setPrevRoute] = useState('notes');
  const value = useSelector<ReduxState, string>(state => state.ui.searchValue);

  const dispatch = useDispatch();
  const { navigate } = useRouter();
  const { route } = useRoute();

  useKeyboard(
    searching
      ? ev => {
          if (ev.key === 'Escape') {
            setSearching(false);
            if (route.name === 'search') {
              navigate(prevRoute);
            }
            inputRef.current!.blur();
          }
        }
      : undefined,
    [navigate, prevRoute, route, searching]
  );

  useEffect(() => {
    if (!searching) {
      dispatch(setSearch(''));
    }
  }, [dispatch, searching]);

  useEffect(() => {
    if (route.name === 'search') {
      inputRef.current!.focus();
      dispatch(setSearch(route.params.value));
    }
  }, [dispatch, route.params.value, route.name]);

  return (
    <HeaderBar expanded={searching}>
      <MenuIcon
        onClick={
          searching
            ? () => {
                setSearching(false);
                if (route.name === 'search') {
                  navigate(prevRoute);
                }
              }
            : () => dispatch(toggleSideBar)
        }
      >
        {searching ? mdiArrowLeft : mdiMenu}
      </MenuIcon>
      <Input
        ref={inputRef}
        type="text"
        placeholder="Search your notes"
        onFocus={() => setSearching(true)}
        value={value}
        onChange={e => {
          dispatch(setSearch(e.target.value));
        }}
        onKeyPress={e => {
          if (e.key === 'Enter') {
            if (route.name !== 'search') {
              setPrevRoute(route.name);
            }
            navigate('search', { value });
            inputRef.current!.blur();
          }
        }}
      />
      <Fade visible={!searching}>
        <AccountIcon onClick={() => setMenuOpened(!menuOpened)}>
          {mdiAccountCircleOutline}
        </AccountIcon>
      </Fade>
      <Fade visible={menuOpened} speed={250}>
        <HeaderAccountMenu
          onHide={() => (menuOpened ? setMenuOpened(false) : null)}
        />
      </Fade>
    </HeaderBar>
  );
};
