import React, { useRef, useEffect } from 'react';

import anime from 'animejs';
import styled from '@emotion/styled';

const hide = (el: HTMLElement, speed: number, keepDisplay?: boolean) =>
  anime({
    targets: el,
    duration: speed,
    easing: 'cubicBezier(0.4, 0.0, 0.2, 1)',
    opacity: [1, 0],
    complete: keepDisplay ? undefined : () => (el.style.display = 'none')
  });
const show = (el: HTMLElement, speed: number, keepDisplay?: boolean) =>
  anime({
    begin: keepDisplay ? undefined : () => (el.style.display = 'initial'),
    targets: el,
    duration: speed,
    easing: 'cubicBezier(0.4, 0.0, 0.2, 1)',
    opacity: [0, 1]
  });

const Span = styled.span`
  display: none;
  opacity: 0;
`;

interface FadeProps extends React.HTMLAttributes<HTMLSpanElement> {
  visible: boolean;
  keepDisplay?: boolean;
  speed?: number;
}
export const Fade: React.FC<FadeProps> = ({
  children,
  visible,
  keepDisplay,
  speed = 100,
  ...props
}) => {
  const didMountRef = useRef(false);
  const ref = useRef<HTMLSpanElement>(null);

  useEffect(() => {
    const el = ref.current!;
    if (!didMountRef.current) {
      didMountRef.current = true;
      if (!visible) {
        return;
      }
    }
    const anim = visible
      ? show(el, speed, keepDisplay)
      : hide(el, speed, keepDisplay);
    return anim.pause;
  }, [visible, speed, keepDisplay]);

  useEffect(() => {
    if (keepDisplay) {
      ref.current!.style.display = 'initial';
    }
  }, [keepDisplay]);

  return (
    <Span ref={ref} {...props}>
      {children}
    </Span>
  );
};
