import React from 'react';

import styled from '@emotion/styled';
import { ThemeProps } from '../../theme';

const EmpDiv = styled.div<ThemeProps>`
  display: block;
  padding: auto;
  cursor: pointer;
  & > svg > path {
    fill: ${({ theme }) => theme.color.primary};
  }
  :hover {
    background: ${({ theme }) => theme.background.hover};
    border-radius: 50%;
  }
  :focus {
    background: ${({ theme }) => theme.background.hover};
    border-radius: 50%;
  }
`;

const Div = styled.div<ThemeProps>`
  display: block;
  padding: auto;
  cursor: pointer;
  & > svg > path {
    fill: ${({ theme }) => theme.color.primaryLight};
  }
  :hover {
    & > svg > path {
      fill: ${({ theme }) => theme.color.primary};
    }
  }
  :focus {
    & > svg > path {
      fill: ${({ theme }) => theme.color.primary};
    }
  }
`;
interface IconProps
  extends React.DetailedHTMLProps<
    React.HTMLAttributes<HTMLDivElement>,
    HTMLDivElement
  > {
  children: string;
  width?: string;
  height?: string;
}
export const Icon: React.FC<IconProps> = ({
  children,
  width = '1.5em',
  height = '1.5em',
  ...props
}) => {
  return (
    <Div {...props}>
      <svg style={{ width, height }} viewBox="0 0 24 24">
        <path d={children} />
      </svg>
    </Div>
  );
};
export const EmpIcon: React.FC<IconProps> = ({
  children,
  width = '1.5em',
  height = '1.5em',
  ...props
}) => {
  return (
    <EmpDiv {...props}>
      <svg style={{ width, height }} viewBox="0 0 24 24">
        <path d={children} />
      </svg>
    </EmpDiv>
  );
};
