import React, { useRef, useEffect } from 'react';

import anime from 'animejs';
import styled from '@emotion/styled';
import { ThemeProps } from '../../../theme';

const expand = (el: HTMLElement) =>
  anime({
    targets: el,
    duration: 250,
    easing: 'cubicBezier(0.4, 0.0, 0.2, 1)',
    margin: ['1em', '0'],
    marginBottom: '1em',
    borderRadius: ['0.5em', '0']
  });
const collapse = (el: HTMLElement) =>
  anime({
    targets: el,
    duration: 200,
    easing: 'cubicBezier(0.4, 0.0, 0.2, 1)',
    margin: ['0', '1em'],
    marginBottom: '1em',
    borderRadius: ['0', '0.5em']
  });

const StyledHeaderBar = styled.header<ThemeProps>`
  background: ${({ theme }) => theme.background.primary};
  color: ${({ theme }) => theme.color.primary};
  display: flex;
  align-items: center;
  height: 3.5em;
  margin-bottom: 1em;
  border-bottom: 1px solid ${({ theme }) => theme.color.border};
  box-shadow: 0px 1px 3px 0px rgba(0, 0, 0, 0.2),
    0px 1px 1px 0px rgba(0, 0, 0, 0.16), 0px 2px 1px -1px rgba(0, 0, 0, 0.12);
`;

interface HeaderBarProps
  extends ThemeProps,
    React.DetailedHTMLProps<React.HTMLAttributes<HTMLElement>, HTMLElement> {
  expanded: boolean;
}
export const HeaderBar: React.FC<HeaderBarProps> = ({ expanded, ...props }) => {
  const ref = useRef<HTMLElement>(null);

  useEffect(() => {
    const el = ref.current!;
    const anim = expanded ? expand(el) : collapse(el);
    return anim.pause;
  }, [expanded]);

  return <StyledHeaderBar ref={ref} {...props} />;
};
