import React, { useRef, useEffect } from 'react';
import { useClick } from '../../components/useClick/useClick';

import anime from 'animejs';
import styled from '@emotion/styled';
import {
  mdiLightbulbOutline,
  mdiBellOutline,
  mdiLabelOutline,
  mdiPencilOutline,
  mdiPackageDown,
  mdiTrashCanOutline
} from '@mdi/js';
import { ThemeProps } from '../../theme';
import {
  Menu as UMenu,
  MenuItem,
  Seperator,
  StaticMenuItem,
  Icon as UIcon,
  WithIcon
} from '../../components/Menu/Menu';

import { useDispatch, useSelector } from 'react-redux';
import { ReduxState } from '../../store/store';
import { toggleSideBar } from '../../store/actions/ui';
import { useRouter } from 'react-router5';

const expand = (sideBar: HTMLElement) =>
  anime({
    targets: sideBar,
    duration: 250,
    easing: 'cubicBezier(0.4, 0.0, 0.2, 1)',
    left: ['-250px', '0']
  });
const collapse = (sideBar: HTMLElement) =>
  anime({
    targets: sideBar,
    duration: 250,
    easing: 'cubicBezier(0.4, 0.0, 0.2, 1)',
    left: ['0', '-250px']
  });

const Menu = styled(UMenu)`
  position: fixed;
  top: 0;
  bottom: 0;
  left: -250px;
  width: 250px;
  height: 100vh;
  z-index: 4;
`;

const Icon = styled(UIcon)`
  margin-right: 1.5em;
  margin-left: 0.5em;
`;

const Title = styled.h1`
  cursor: pointer;
  font-size: 2rem;
  font-weight: normal;
  margin: 0;
  padding: 0;
  text-align: center;
  width: 100%;
`;

const Footer = styled.small<ThemeProps>`
  color: ${({ theme }) => theme.color.primaryLight};
`;

export const SideBar: React.FC = () => {
  const expanded = useSelector<ReduxState, boolean>(
    state => state.ui.sideBarOpen
  );
  const didMountRef = useRef(false);
  const menuRef = useRef<HTMLDivElement>(null);

  const { navigate } = useRouter();
  const dispatch = useDispatch();

  useEffect(() => {
    const menu = menuRef.current!;
    if (!didMountRef.current) {
      didMountRef.current = true;
      return;
    }
    const anim = expanded ? expand(menu) : collapse(menu);
    return anim.pause;
  }, [expanded]);

  useClick(
    expanded
      ? ev => {
          if (!expanded) {
            return undefined;
          }
          const menu = menuRef.current!;
          if (!menu.contains(ev.target as Node)) {
            dispatch(toggleSideBar);
          }
        }
      : undefined,
    [expanded]
  );

  const wrapClick = (fun: () => any) => () => {
    dispatch(toggleSideBar);
    fun();
  };

  return (
    <Menu ref={menuRef}>
      <StaticMenuItem onClick={wrapClick(() => navigate('notes'))}>
        <Title>FH Notes</Title>
      </StaticMenuItem>
      <MenuItem onClick={wrapClick(() => navigate('notes'))}>
        <WithIcon>
          <Icon>{mdiLightbulbOutline}</Icon>
          Notes
        </WithIcon>
      </MenuItem>
      <MenuItem onClick={wrapClick(() => navigate('reminders'))}>
        <WithIcon>
          <Icon>{mdiBellOutline}</Icon>
          Reminders
        </WithIcon>
      </MenuItem>
      <Seperator />
      <MenuItem onClick={wrapClick(() => navigate('label', { name: 'Ideas' }))}>
        <WithIcon>
          <Icon>{mdiLabelOutline}</Icon>
          Ideas
        </WithIcon>
      </MenuItem>
      <MenuItem
        onClick={wrapClick(() => navigate('label', { name: 'Personal' }))}
      >
        <WithIcon>
          <Icon>{mdiLabelOutline}</Icon>
          Personal
        </WithIcon>
      </MenuItem>
      <MenuItem onClick={wrapClick(() => navigate('label', { name: 'Work' }))}>
        <WithIcon>
          <Icon>{mdiLabelOutline}</Icon>
          Work
        </WithIcon>
      </MenuItem>
      <MenuItem onClick={wrapClick(() => navigate('labeledit'))}>
        <WithIcon>
          <Icon>{mdiPencilOutline}</Icon>
          Edit Labels
        </WithIcon>
      </MenuItem>
      <Seperator />
      <MenuItem onClick={wrapClick(() => navigate('archive'))}>
        <WithIcon>
          <Icon>{mdiPackageDown}</Icon>
          Archive
        </WithIcon>
      </MenuItem>
      <MenuItem onClick={wrapClick(() => navigate('deleted'))}>
        <WithIcon>
          <Icon>{mdiTrashCanOutline}</Icon>
          Deleted Items
        </WithIcon>
      </MenuItem>
      <Seperator />
      <StaticMenuItem>
        <WithIcon>
          <Footer>Created by Fawaz Hussain</Footer>
        </WithIcon>
      </StaticMenuItem>
    </Menu>
  );
};
