export enum Themes {
  LIGHT,
  DARK
}

export interface Theme {
  color: {
    primary: string;
    primaryLight: string;
    secondary: string;
    border: string;
  };
  background: {
    primary: string;
    secondary: string;
    hover: string;
  };
}

export interface ThemeProps {
  theme?: Theme;
}

