import React, { useEffect, useRef } from 'react';

import anime from 'animejs';
import styled from '@emotion/styled';
import {
  useNote,
  Card as UCard,
  TopBar,
  Icon,
  mdiPinOutline,
  TitleInput,
  Body,
  BodyInput,
  NoteActions
} from '../../Note/Note';

const expand = (el: HTMLElement) =>
  anime({
    targets: el,
    duration: 200,
    easing: 'cubicBezier(0.4, 0.0, 0.2, 1)',
    bottom:
      window.innerWidth <= 480
        ? ['calc(-100% + 8em)', '0%']
        : ['calc(-40% + 3em)', '0%'],
    begin: () => {
      el.style.display = 'flex';
    }
  });

const collapse = (el: HTMLElement) =>
  anime({
    complete: () => {
      el.style.display = 'none';
    },
    targets: el,
    duration: 250,
    easing: 'cubicBezier(0.4, 0.0, 0.2, 1)',
    bottom:
      window.innerWidth <= 480 ? ['0%', 'calc(-100% + 5em)'] : ['0%', '-40%']
  });

const Card = styled(UCard)`
  display: none;
  position: fixed;
  bottom: calc(-40% + 3em);
  width: 100%;
  height: 40%;
  z-index: 2;
  border-radius: 0;
  @media (max-width: 480px) {
    height: calc(100% - 5em);
    bottom: calc(-100% + 8em);
  }
`;

interface NewNoteProps {
  onClose: () => void;
  opened: boolean;
}
export const NewNote: React.FC<NewNoteProps> = ({ onClose, opened }) => {
  const cardRef = useRef<HTMLDivElement>(null);
  const didMount = useRef(false);

  useEffect(() => {
    if (!didMount.current) {
      didMount.current = true;
      return;
    }
    const card = cardRef.current!;
    const anim = opened ? expand(card) : collapse(card);
    return anim.pause;
  }, [opened]);

  useNote(opened, onClose, cardRef);

  return (
    <Card ref={cardRef}>
      <TopBar>
        <TitleInput type="text" placeholder="Title" />
        <Icon>{mdiPinOutline}</Icon>
      </TopBar>
      <Body>
        <BodyInput rows={1} placeholder="Take a note..." />
      </Body>
      <NoteActions opened={opened} onClose={onClose} />
    </Card>
  );
};
