import { Action } from 'redux';
import { Themes } from '../../theme';

export enum UIActions {
  SEARCH = 'UI2',
  TOGGLE_DARK_THEME = 'UI0',
  TOGGLE_SIDE_BAR = 'UI1'
}
export interface UIAction extends Action<UIActions> {
  theme?: Themes;
  searchValue?: string;
}

export const setSearch = (searchValue: string): UIAction => ({
  type: UIActions.SEARCH,
  searchValue
});

export const toggleDarkTheme = (theme: Themes): UIAction => ({
  type: UIActions.TOGGLE_DARK_THEME,
  theme
});

export const toggleSideBar = {
  type: UIActions.TOGGLE_SIDE_BAR
};
