import React from 'react';

import styled from '@emotion/styled';
import { mdiCheckBoxOutline, mdiImageOutline } from '@mdi/js';
import { Card as UCard } from '../../../components/Card/Card';
import { Icon } from '../../../components/Icon/Icon';
import { Input as UInput } from '../../../components/Input/Input';

const Card = styled(UCard)`
  position: fixed;
  bottom: 0;
  border-radius: 0;
  width: 100%;
  height: 3em;
  padding: 0;
  z-index: 2;
`;
const Actions = styled.div`
  display: flex;
  padding-right: 0.5em;
`;
const Action = styled(Icon)`
  padding: 0.5em;
  width: 2.5em;
  height: 2.5em;
`;
const Input = styled(UInput)`
  padding-left: 0.5em;
`;

interface NewActionBarProps {
  onNew: (options: { checked?: boolean; image?: boolean }) => void;
}
export const NewActionBar: React.FC<NewActionBarProps> = ({ onNew }) => {
  return (
    <Card>
      <Input
        onClick={() => onNew({})}
        readOnly={true}
        type="text"
        placeholder="Take a note..."
      />
      <Actions>
        <Action onClick={() => onNew({ checked: true })}>
          {mdiCheckBoxOutline}
        </Action>
        <Action onClick={() => onNew({ image: true })}>
          {mdiImageOutline}
        </Action>
      </Actions>
    </Card>
  );
};
