import React, { useRef, useState, useEffect, useLayoutEffect } from 'react';

import anime from 'animejs';
import styled from '@emotion/styled';
import {
  useNote,
  Card as UCard,
  TopBar,
  Icon,
  mdiPinOutline,
  TitleInput,
  Body as UBody,
  NoteActions
} from '../../Note/Note';
import { Fade } from '../../../components/Fade/Fade';
import { ThemeProps } from '../../../theme';

const Backdrop = styled.div<ThemeProps>`
  width: 100%;
  height: 100%;
  position: fixed;
  z-index: 3;
  left: 0;
  top: 0;
  background: ${({ theme }) => theme.background.secondary}C0;
`;

const expand = (
  el: HTMLDivElement,
  top: string,
  left: string,
  width: string,
  toTop: string,
  toWidth: string
) =>
  anime({
    targets: el,
    duration: 250,
    easing: 'cubicBezier(0.4, 0.0, 0.2, 1)',
    top: [top, toTop],
    left: [left, '16px'],
    width: [width, toWidth]
  });
const collapse = (
  el: HTMLDivElement,
  top: string,
  left: string,
  width: string,
  toTop: string,
  toWidth: string
) =>
  anime({
    targets: el,
    duration: 250,
    easing: 'cubicBezier(0.4, 0.0, 0.2, 1)',
    top: [toTop, top],
    left: ['16px', left],
    width: [toWidth, width]
  });

const Placeholder = styled.div`
  height: 0;
  margin: 0;
  display: none;
`;
const Card = styled(UCard)`
  height: auto;
  box-sizing: border-box;
`;
const Body = styled(UBody)`
  height: auto;
  overflow: auto;
  position: relative;
  text-align: initial;
`;
const BodyInput = styled.div`
  font-size: 0.8rem;
  margin: 0;
  height: auto;
  min-height: 1.5em;
`;
const BodyHint = styled.div`
  display: block;
  font-size: 0.8rem;
  color: #80868b;
  pointer-events: none;
  position: absolute;
`;
const SFade = styled(Fade)`
  width: 100%;
`;

const parse = (input: string) => {
  return input
    .replace('<div>', '')
    .replace('</div>', '')
    .trim();
};

interface NoteProps {}
export const Note: React.FC<NoteProps> = () => {
  const initialRectRef = useRef<ClientRect & { scroll?: number } | null>(null);
  const cardRef = useRef<HTMLDivElement>(null);
  const placeHolderRef = useRef<HTMLDivElement>(null);
  const bodyInputRef = useRef<HTMLDivElement>(null);
  const didMount = useRef(false);
  const [hover, setHover] = useState(false);
  const [body, setBody] = useState('');
  const [animOpen, setAnimOpen] = useState(false);
  const [open, setOpen] = useState(false);

  useLayoutEffect(() => {
    if (!didMount.current) {
      didMount.current = true;
      return;
    }
    const card = cardRef.current!;
    const placeHolder = placeHolderRef.current!;
    const cRect = card.getBoundingClientRect();
    const iRect = initialRectRef.current!;
    const scrollOffset = iRect.scroll! - window.scrollY;
    const cTop = `${animOpen ? cRect.top : iRect.top + scrollOffset}px`;
    const cLeft = `${iRect.left}px`;
    const cWidth = `${animOpen ? cRect.width : iRect.width}px`;
    const toTop = `${window.innerHeight * 0.2}px`;
    const toWidth = `${document.body.clientWidth - 32}px`;
    card.style.width = 'auto';
    console.log([cTop, cLeft, cWidth]);
    if (animOpen) {
      card.style.zIndex = '4';
      placeHolder.style.height = `${card.offsetHeight}px`;
      placeHolder.style.display = 'block';
      card.style.left = cLeft;
      card.style.position = 'fixed';
      card.style.maxHeight = 'calc(80% - 4em)';
      const anim = expand(card, cTop, cLeft, cWidth, toTop, toWidth);
      anim.finished.then(() => {
        setOpen(true);
      });
      return anim.pause;
    }
    const anim = collapse(card, cTop, cLeft, cWidth, toTop, toWidth);
    anim.finished.then(() => {
      card.style.maxHeight = null;
      card.style.zIndex = 'auto';
      placeHolder.style.display = 'none';
      card.style.position = 'initial';
      card.style.width = '100%';
      setOpen(false);
    });
    return anim.pause;
  }, [animOpen]);

  useNote(open, () => setAnimOpen(false), cardRef);

  useEffect(() => {
    const bodyInput = bodyInputRef.current!;
    const observer = new MutationObserver((_, __) => {
      const html = bodyInput.innerHTML;
      setBody(parse(html));
    });
    observer.observe(bodyInput, {
      childList: true,
      subtree: true,
      characterData: true,
      attributes: true
    });
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  const onOpen = () => {
    if (!open) {
      initialRectRef.current = cardRef.current!.getBoundingClientRect() as ClientRect;
      initialRectRef.current.scroll = window.scrollY;
      setAnimOpen(true);
    }
  };

  return (
    <>
      <Card
        ref={cardRef}
        onMouseEnter={() => setHover(true)}
        onMouseLeave={() => setHover(false)}
      >
        <TopBar>
          <TitleInput
            onClick={onOpen}
            type="text"
            placeholder={animOpen ? 'Title' : ''}
          />
          <Fade visible={hover || animOpen} keepDisplay>
            <Icon>{mdiPinOutline}</Icon>
          </Fade>
        </TopBar>
        <Body onClick={onOpen}>
          <BodyHint>
            {body.length === 0 ? (animOpen ? 'Note' : 'Empty Note') : ''}
          </BodyHint>
          <BodyInput contentEditable ref={bodyInputRef} />
        </Body>
        <SFade visible={hover || animOpen} keepDisplay>
          <NoteActions
            opened={open && animOpen}
            onClose={() => setOpen(false)}
          />
        </SFade>
      </Card>
      <Placeholder ref={placeHolderRef} />
      <Fade visible={animOpen}>
        <Backdrop />
      </Fade>
    </>
  );
};
