import React from 'react';

import styled from '@emotion/styled';
import { ThemeProps } from '../../theme';

const USwitch = styled.span<ThemeProps>`
  position: relative;
  display: inline-block;
  width: 3em;
  height: 1.5em;
  padding: 0.1em;
  border-radius: 1em;
  background: ${({ theme }) => theme.background.hover};
`;

const USlider = styled.span<ThemeProps & { enabled: boolean }>`
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: ${({ enabled, theme }) =>
    enabled ? theme.color.secondary : theme.background.hover};
  transition: 0.2s;
  border-radius: 1em;
  &::before {
    position: absolute;
    content: '';
    height: 1.2em;
    width: 1.2em;
    left: 0.3em;
    bottom: 0.15em;
    background-color: ${({ theme }) => theme.color.primary};
    transition: 0.2s;
    border-radius: 50%;
    ${({ enabled }) => (enabled ? 'transform: translateX(1.2em)' : '')}
  }
`;

interface SwitchProps {
  value: boolean;
  onSwitch: (enabled: boolean) => void;
}
export const Switch: React.FC<SwitchProps> = ({ value, onSwitch }) => {
  return (
    <USwitch onClick={() => onSwitch(!value)}>
      <USlider enabled={value} />
    </USwitch>
  );
};
