import React, { useState } from 'react';

import { NewActionBar } from './ActionBar/NewActionBar';
import { NewNote } from './Note/NewNote';

interface onNewParams {
  checked?: boolean;
  image?: boolean;
}
export const New: React.FC = () => {
  const [newMode, setNewMode] = useState(false);

  const onNew = (_: onNewParams) => {
    setNewMode(true);
  };

  const onClose = () => {
    setNewMode(false);
  };

  return (
    <>
      <NewActionBar onNew={onNew} />
      <NewNote opened={newMode} onClose={onClose} />
    </>
  );
};
