import React, { useRef, useEffect } from 'react';
import { useClick } from '../../../components/useClick/useClick';
import { useDispatch, useSelector } from 'react-redux';
import { useRouter } from 'react-router5';

import styled from '@emotion/styled';
import {
  mdiAccountCircleOutline,
  mdiAccountMultiple,
  mdiSettings,
  mdiWeatherNight,
  mdiLoginVariant,
  mdiLogoutVariant
} from '@mdi/js';
import { Themes } from '../../../theme';
import { Switch } from '../../../components/Switch/Switch';
import {
  Menu as UMenu,
  MenuItem,
  Seperator,
  StaticMenuItem,
  Icon,
  WithIcon
} from '../../../components/Menu/Menu';

import { ReduxState } from '../../../store/store';
import { toggleDarkTheme } from '../../../store/actions/ui';

const Menu = styled(UMenu)`
  opacity: 1;
  position: absolute;
  right: 1em;
  top: 5em;
  width: 200px;
  max-height: calc(100vh - 8.5em);
  border-radius: 0.25em;
`;

interface HeaderAccountMenuProps {
  username?: string;
  onHide: () => void;
}
export const HeaderAccountMenu: React.FC<HeaderAccountMenuProps> = ({
  username,
  onHide
}) => {
  const dispatch = useDispatch();
  const { navigate } = useRouter();
  const theme = useSelector<ReduxState, Themes>(state => state.ui.theme);
  const menuRef = useRef<HTMLDivElement>(null);

  useClick(
    ev => {
      const menu = menuRef.current!;
      if (!menu.contains(ev.target as Node)) {
        onHide();
      }
    },
    [onHide]
  );
  useEffect(() => {
    const clicked = (ev: MouseEvent) => {
      const menu = menuRef.current!;
      if (!menu.contains(ev.target as Node)) {
        onHide();
      }
    };
    window.addEventListener('click', clicked);
    return () => window.removeEventListener('click', clicked);
  }, [onHide]);

  const wrapClicks = (fun: () => any) => () => {
    onHide();
    fun();
  };

  return (
    <Menu ref={menuRef}>
      {username ? (
        <>
          <StaticMenuItem>
            <WithIcon>
              <Icon>{mdiAccountCircleOutline}</Icon>
              Username
            </WithIcon>
          </StaticMenuItem>
          <MenuItem onClick={wrapClicks(() => navigate('friends'))}>
            <WithIcon>
              <Icon>{mdiAccountMultiple}</Icon>
              Friends
            </WithIcon>
          </MenuItem>
          <Seperator />
        </>
      ) : null}
      <MenuItem onClick={wrapClicks(() => navigate('settings'))}>
        <WithIcon>
          <Icon>{mdiSettings}</Icon>
          Settings
        </WithIcon>
      </MenuItem>
      <StaticMenuItem>
        <WithIcon>
          <Icon>{mdiWeatherNight}</Icon>
          Dark Theme
        </WithIcon>
        <Switch
          value={theme === Themes.DARK}
          onSwitch={value =>
            dispatch(toggleDarkTheme(value ? Themes.DARK : Themes.LIGHT))
          }
        />
      </StaticMenuItem>
      <Seperator />
      <MenuItem
        onClick={username ? undefined : wrapClicks(() => navigate('login'))}
      >
        <WithIcon>
          <Icon>{username ? mdiLogoutVariant : mdiLoginVariant}</Icon>
          {username ? 'Log Out' : 'Log In'}
        </WithIcon>
      </MenuItem>
    </Menu>
  );
};
