import { UIAction, UIActions } from '../actions/ui';
import { Themes } from '../../theme';

export interface UIState {
  searchValue: string;
  sideBarOpen: boolean;
  theme: Themes;
}

const initialState: UIState = {
  searchValue: '',
  sideBarOpen: false,
  theme: Themes.LIGHT
};

export const uiReducer = (state = initialState, action: UIAction): UIState => {
  switch (action.type) {
    case UIActions.SEARCH:
      return {
        ...state,
        searchValue: action.searchValue!
      };
    case UIActions.TOGGLE_DARK_THEME:
      return {
        ...state,
        theme: action.theme!
      };
    case UIActions.TOGGLE_SIDE_BAR:
      return {
        ...state,
        sideBarOpen: !state.sideBarOpen
      };
    default:
      return state;
  }
};
