import React from 'react';
import { useClick } from '../../components/useClick/useClick';

import styled from '@emotion/styled';
import {
  mdiPinOutline,
  mdiBellPlusOutline,
  mdiAccountPlusOutline,
  mdiPaletteOutline,
  mdiImageOutline,
  mdiPackageDown,
  mdiLabelOutline,
  mdiCheckBoxOutline,
  mdiUndo,
  mdiRedo
} from '@mdi/js';
import { ThemeProps } from '../../theme';
import { Card as UCard } from '../../components/Card/Card';
import { Icon } from '../../components/Icon/Icon';
import { Input, TextArea } from '../../components/Input/Input';

export const Card = styled(UCard)`
  padding: 0.25em;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
`;

export const TopBar = styled.div`
  width: 100%;
  display: flex;
  align-items: center;
  padding: 0.25em 0.5em;
`;

export { mdiPinOutline, Icon };

export const TitleInput = styled(Input)`
  font-size: 1.2rem;
  height: 2em;
  margin: 0;
  font-weight: 500;
  &::placeholder {
    font-weight: normal;
  }
`;

export const Body = styled.div`
  width: 100%;
  flex: 1;
  padding: 0.25em 0.5em;
`;
export const BodyInput = styled(TextArea)`
  font-size: 0.8rem;
  margin: 0;
`;

const Actions = styled.div`
  width: 100%;
  height: 1.875rem;
  overflow: hidden;
  box-sizing: content-box;
`;
const Scroll = styled.div`
  display: flex;
  min-width: 100%;
  height: 100%;
  overflow-x: auto;
  white-space: nowrap;
  -webkit-overflow-scrolling: touch;
  box-sizing: content-box;
  padding-bottom: 32px;
`;
const Action = styled(Icon)`
  font-size: 0.75rem;
  width: 1.125rem;
  height: 1.125rem;
  padding: 0.5em 1em;
  box-sizing: content-box;
`;
const CloseButton = styled.span<ThemeProps>`
  display: flex;
  align-items: center;
  height: 1.125rem;
  cursor: pointer;
  margin-left: auto;
  margin-right: 1em;
  padding: 0.5em 2em;
  font-size: 0.75rem;
  font-weight: 600;
  :hover {
    background: ${({ theme }) => theme.background.hover};
  }
  @media (max-width: 480px) {
    padding: 0.5em 1em;
  }
`;

export const useNote = (
  opened: boolean,
  onClose: () => void,
  cardRef: React.RefObject<HTMLDivElement>
) =>
  useClick(
    opened
      ? ev => {
          const card = cardRef.current!;
          if (!card.contains(ev.target as Node)) {
            onClose();
          }
        }
      : undefined,
    [opened]
  );

interface NoteActionsProps {
  opened: boolean;
  onClose: () => void;
}
export const NoteActions: React.FC<NoteActionsProps> = ({
  opened,
  onClose
}) => {
  return (
    <Actions>
      <Scroll>
        <Action>{mdiBellPlusOutline}</Action>
        <Action>{mdiAccountPlusOutline}</Action>
        <Action>{mdiPaletteOutline}</Action>
        <Action>{mdiImageOutline}</Action>
        <Action>{mdiPackageDown}</Action>
        <Action>{mdiLabelOutline}</Action>
        {opened ? (
          <>
            <Action>{mdiCheckBoxOutline}</Action>
            <Action>{mdiUndo}</Action>
            <Action>{mdiRedo}</Action>
          </>
        ) : null}
        {opened ? <CloseButton onClick={onClose}>Close</CloseButton> : null}
      </Scroll>
    </Actions>
  );
};
