import React, { useState, useEffect } from 'react';
import { useSelector } from 'react-redux';
import { useRouteNode } from 'react-router5';

import styled from '@emotion/styled';
import { Header } from '../Header/Header';
import { New } from '../New/New';
import { SideBar } from '../SideBar/SideBar';

import { ThemeProvider } from 'emotion-theming';
import { ReduxState } from '../../store/store';
import { darkTheme, lightTheme, ThemeProps, Themes } from '../../theme';
import { Notes } from '../Notes/Notes';

export const Container = styled.div<ThemeProps>`
  background: ${({ theme }) => theme.background.secondary};
  color: ${({ theme }) => theme.color.primary};
  display: flex;
  flex-direction: column;
  width: 100%;
  min-height: 100vh;
  text-align: center;
`;

export const App: React.FC = () => {
  const [el, setEl] = useState<JSX.Element | null>(null);
  const { route } = useRouteNode('');

  const theme = useSelector<ReduxState, Themes>(state => state.ui.theme);

  useEffect(() => {
    switch (route.name) {
      case 'notes':
        setEl(<Notes />);
        break;
      default:
        setEl(<main>404 Not Found</main>);
    }
  }, [route.name]);

  return (
    <ThemeProvider theme={theme === Themes.LIGHT ? lightTheme : darkTheme}>
      <Container>
        <Header />
        {el}
        <New />
        <SideBar />
      </Container>
    </ThemeProvider>
  );
};
